@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300;400;500;600&display=swap');



* {
  margin: 1;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --color-bg: rgb(20, 20, 20);
  --color-bg-variant: #004b6b95;
  --color-primary: rgb(235, 172, 76);
  --color-primary-variant: #03a0a897;
  --color-white: blanchedalmond;
  --color-light: rgba( 255, 255, 255, 0.6);

  --transition: all 400ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: 'Josefin Sans', sans-serif;
  background: var(--color-bg);
  color: var(--color-white);
  line-height: 1-7;
  background-image: url();
  

}

.wave {
  fill: rgba(26, 34, 51, 0.38);
  width: 100vw;
  height: 65vh;
  z-index: -1;
  position: fixed;
  bottom: 0;
  left: 0;
}

/*---------Styles---------*/

.conteiner {
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
  margin-top: 1rem;
}

h1 {
  font-size: 2.5rem;
  
}

section {
  margin-top: 8rem;
  
}

section > h2,
section > h5 {
  text-align: center;
  color: var(--color-light);
}

section > h2 {
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-light {
  color: var(--color-light);
}

a {
  color: var(--color-primary);
  transition: var(--transition);
}

a:hover {
  color: var(--color-white);
}

.btn {
  width: max-content;
  display: inline-block;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
}

.btn:hover {
  background: var(--color-white);
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary {
  background: var(--color-primary);
  color: var(--color-bg)
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/*----Media Queries-Medium Devices-----*/

@media screen and  (max-width: 1024px) {
  .container {
    width: var(--container-width-md);
  }
  
  section {
    margin-top: 6rem;
  }
}

/*----Media Queries-Small Devices-----*/

@media screen and  (max-width: 600px) {
  .container {
    width: var(--container-width-sm);
  }
  
  section > h2 {
    margin-top: 2rem;
  }
}


